.read-more {
  display: flex;
  flex-direction: column;

  &__wrapper {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-auto-rows: minmax(246px, 1fr);
    margin-top: 80px;
    gap: 10px;

    @media (max-width: 940px) {
      grid-template-columns: 1fr 1fr;
    }
  
    @media (max-width: 740px) {
      grid-template-columns: 1fr;
      grid-auto-rows: auto;
      margin-top: 40px;
    }
  }

  &__title {
    font-weight: 400;
    font-size: 34px;
    line-height: 126%;
    color: #19191D;
    margin-bottom: 30px;
    
    b { color: #B4B4BB; }
  }

  &__load-btn {
    margin-top: 30px;
    align-self: center;
  }
}