@keyframes scale {
  from {
    scale: 1;
  }
  50% {
    scale: 1.2;
  }
  to {
    scale: 1;
  }
}
@keyframes jump {
  from {
    translate: 0 0;
  }
  50% {
    translate: 0 20px;
  }
  to {
    translate: 0 0;
  }
}
@keyframes jump10px {
  from {
    translate: 0 0;
  }
  50% {
    translate: 0 10px;
  }
  to {
    translate: 0 0;
  }
}
@keyframes dot-flashing {
  from {
    opacity: 1;
  }
  50% {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@keyframes spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
@keyframes calc-loader {
  from {
    left: -100%;
  }
  to {
    left: 100%;
  }
}
