.accordeon {
  padding: 20px 0;
  display: grid;
  gap: 10px 30px;
  grid-template-columns: 1fr auto;
  align-items: center;
  border-bottom: 1px solid #D2D2D6;
  cursor: pointer;
  user-select: none;
}
.accordeon--with-switch {
  grid-template-columns: 1fr auto auto;
}
@media (max-width: 740px) {
  .accordeon--with-switch {
    grid-template-columns: 1fr auto;
  }
}
.accordeon--with-switch:has(.accordeon-switch-hide:checked) + .subaccordeon .accordeon-elem-allow-hidden {
  display: none;
}
.accordeon--with-switch:not(.--active) .switch {
  opacity: 0;
  pointer-events: none;
}
.accordeon--with-switch .switch {
  transition: 0.3s ease;
}
@media (max-width: 740px) {
  .accordeon--for-mobile {
    padding: 0;
    border-bottom: none;
    pointer-events: none;
  }
}
.accordeon > p {
  font-weight: 400;
  font-size: 20px;
  line-height: 125%;
  color: #19191D;
  display: flex;
  align-items: center;
  gap: 10px;
  transition: 0.2s ease;
}
.accordeon > p b {
  font-weight: 400;
  font-size: 12px;
  line-height: 15px;
  color: #787885;
}
@media (max-width: 740px) {
  .accordeon--for-mobile > p b {
    display: none;
  }
}
.accordeon > .btn {
  position: relative;
}
@media (max-width: 740px) {
  .accordeon--with-switch > .btn {
    grid-column: 2;
    grid-row: 1/3;
  }
}
@media (max-width: 740px) {
  .accordeon--for-mobile > .btn {
    display: none;
  }
}
.accordeon > .btn i {
  transition: 0.2s ease;
}
.accordeon > .btn .icon-close, .accordeon > .btn .icon-minus {
  position: absolute;
  opacity: 0;
  transform: scale(0);
