.btn {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 14px 30px;
  gap: 10px;
  background: #5A5B6A;
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.08);
  border-radius: 8px;
  font-weight: 500;
  font-size: 14px;
  line-height: 130%;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  color: #FFF;
  transition: 0.2s ease;
  border: 1px solid transparent;
}
.btn i {
  font-size: 1.3em;
  color: inherit;
}
.btn:not(:has(span)) {
  padding: 14px;
}
.btn[disabled] {
  pointer-events: none;
  opacity: 0.3;
}
@media (max-width: 1230px) {
  .btn span.--media-min-940px {
    display: none;
  }
}
@media (min-width: 1231px) {
  .btn span.--media-max-940px {
    display: none;
  }
}
.btn--small {
  padding: 10px 20px;
  font-size: 12px;
}
.btn--small:not(:has(span)) {
  padding: 10px;
}
.btn--large {
  padding: 20px 40px;
  font-size: 16px;
}
.btn--large:not(:has(span)) {
  padding: 20px;
}
.btn--lime {
  background: #CCFF99;
  color: #19191D;
}
.btn--outline {
  color: #19191D;
  background: #FFFFFF;
  border-color: #D2D2D6;
}
.btn--secondary {
  background: #F9F9FA;
  border-color: transparent;
  color: #19191D;
}
.btn--text {
  background: transparent;
  color: #19191D;
  box-shadow: none;
}
.btn--toggle {
  position: relative;
}
.btn--toggle i {
  transition: 0.2s ease;
}
.btn--toggle i:nth-of-type(2) {
  position: absolute;
  opacity: 0;
  transform: scale(0);
}
.btn--toggle.--opened i:nth-of-type(1) {
  opacity: 0;
  transform: scale(0);
}
.btn--toggle.--opened i:nth-of-type(2) {
  opacity: 1;
  transform: scale(1);
}
.btn--toggle span._off {
  display: none;
}
@media (any-hover: hover) {
  .btn:hover, .btn.--hover {
    background: #292A31;
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
  }
  .btn--lime:hover, .btn--lime.--hover {
    background: #bae88b;
  }
  .btn--secondary:hover, .btn--secondary.--hover {
    background: #F0F0F1;
  }
  .btn--outline:hover, .btn--outline.--hover {
    border-color: #4A4B57;
    background: #FFFFFF;
  }
  .btn--text:hover, .btn--text.--hover {
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
    background: #FFFFFF;
  }
}
.btn:active {
  background: #292A31;
  box-shadow: 0px 4px 8px transparent;
}
.btn--lime:active {
  background: #708c54;
  color: #d6ffad;
}
.btn--secondary:active {
  background: #E1E1E3;
}
.btn--outline:active {
  background: #D2D2D6;
  border-color: transparent;
}
.btn--text:active {
  box-shadow: none;
  background: #E1E1E3;
}

