  position: absolute;
  inset: 0;
  display: grid;
  grid-auto-flow: column;
  grid-auto-columns: 1fr;
  grid-template-rows: 1fr;
  border: 1px dashed rgba(0, 0, 26, 0.15);
  border-bottom: none;
