.select-item {
  display: flex;
  align-items: center;
  padding: 10px 15px;
  gap: 8px;
  border-radius: 8px;
  background: transparent;
  cursor: pointer;
  user-select: none;
  transition: 0.3s ease;
}
.select-item__input-wrapper {
  position: relative;
  display: block;
  width: 21px;
  height: 21px;
  background: #F9F9FA;
  border: 1px solid #D2D2D6;
  border-radius: 4px;
  transition: 0.3s ease;
}
.select-item__input-wrapper input {
  opacity: 0;
  position: absolute;
  inset: 0;
  cursor: inherit;
}
.select-item__input-wrapper i {
  color: #CCFF99;
  position: absolute;
  top: 50%;
  left: 50%;
  translate: -50% -50%;
  cursor: inherit;
  transform: scale(0);
  transition: 0.3s ease;
}
.select-item__input-wrapper:has(input:checked) {
  background: #5A5B6A;
}
.select-item__input-wrapper:has(input:checked) i {
  transform: scale(1);
}
.select-item--radio .select-item__input-wrapper {
  display: none;
}
.select-item span {
  font-weight: 400;
  cursor: inherit;
  font-size: 16px;
  line-height: 130%;
  color: #19191D;
}
@media (any-hover: hover) {
  .select-item:hover {
    background: #F0F0F1;
  }
}
.select-item:active {
  background: #E1E1E3;
}
.select-item:has(input:checked) {
  background: #E1E1E3;
}
.select-item[disabled] {
  pointer-events: none;
  opacity: 0.3;
}

.select-menu {
  display: flex;
  flex-direction: column;
  padding: 0px;
  background: #FFFFFF;
  box-shadow: 0px 6px 12px rgba(0, 0, 0, 0.1);
  border-radius: 4px;
  width: max-content;
  transition: 0.3s ease;
  max-height: 300px;
  overflow-y: auto;
}
.select-menu--radio {
