}

.select {
  display: flex;
  flex-direction: column;
  position: relative;
  max-width: 100%;
}
.select__label {
  font-weight: 400;
  font-size: 16px;
  line-height: 130%;
  color: #19191D;
  text-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
  margin-bottom: 5px;
  align-self: flex-start;
}
.select--white .select__label {
  color: white;
}
.select__button {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px;
  gap: 10px;
  background: #FFFFFF;
  border: 1px solid #D2D2D6;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
  border-radius: 8px;
  font-weight: 400;
  font-size: 16px;
  line-height: 130%;
  color: #5A5B6A;
  transition: 0.3s ease;
  width: 160px;
  min-width: 100%;
}
.select__button span {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.select__button i {
  display: block;
  transform: rotate(-90deg);
  translate: 1px 1px;
  font-size: 16px;
  line-height: 1;
  transition: 0.3s ease;
}
