  .select__button:hover {
    border-color: #5A5B6A;
  }
}
.select__button[disabled] {
  opacity: 0.3;
  pointer-events: none;
}
.select--radio .select__button {
  width: auto;
  min-width: 0;
}
.select.--active .select__button i {
  transform: rotate(-270deg);
}
.select.--filled .select__button span {
  color: #19191D;
}
.select .select-menu {
  position: absolute;
  top: calc(100% + 5px);
  left: 0;
  z-index: 1;
}
.select:not(.--active) .select-menu {
  opacity: 0;
  pointer-events: none;
}

